.gpt3__blog-container_article{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background: var(--color-footer);
    border-radius: 2rem;
}

.gpt3__blog-container_article-image{
width: 100%;
height: 100%;
background: var(--color-footer);
border-radius: 3rem;

}

.gpt3__blog-container_article-image img{
width: 100%;
height: 100%;
border-radius: 2rem 2rem 0 0;

}

.gpt3__blog-container_article-content{
display: flex;
flex-direction: column;
justify-content: space-between;
padding: 1rem 1.5rem;
height : 100%;
}

.gpt3__blog-container_article-content p{
    font-family: var(--font-family);
    font-weight: bold;
    font-size: 12px;
    line-height: 34px;
    color:white
}

.gpt3__blog-container_article-content h3{
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 25px;
    line-height: 30px;
    color: white;
    margin-bottom: 5rem;
    cursor: pointer;
}

.gpt3__blog-container_article-content p:last-child{
cursor: pointer;
}

@media screen and (max-width: 550px) {
    .gpt3__blog-container_article-content h3{
        font-size: 18px;
        line-height: 25px;
    }
    }

